//
// Variables
//

$badge-height: $global-height-xsmall !default;

$badge-success-enable: true !default;
$badge-danger-enable: true !default;
$badge-rounded-enable: true !default;

//
// Component
//

@mixin badge() {
  .badge {
    background: $global-background;
    border: 1px solid $global-border;
    border-radius: $global-border-radius;
    color: $global-muted;
    cursor: default;
    display: inline-flex;
    align-items: center;
    padding: 0 $global-whitespace-small;
    line-height: $global-line-height;
    font-size: $global-font-size-xsmall;
    font-weight: $global-font-weight-bold;
    height: $badge-height;
    text-align: center;
    text-transform: uppercase;
    user-select: none;

    @if $badge-success-enable {
      &--success {
        border-color: $global-success;
        background: $global-success;
        color: $global-contrast;
      }
    }

    @if $badge-danger-enable {
      &--danger {
        border-color: $global-danger;
        background: $global-danger;
        color: $global-contrast;
      }
    }

    @if $badge-rounded-enable {
      &--rounded {
        border-radius: ($badge-height / 2);
      }
    }

    @if (mixin-exists(hook-badge)) { @include hook-badge(); }
  }
}
