//
// Variables
//

$offcanvas-background: rgba($global-dark-overlay, $global-disabled-opacity) !default;
$offcanvas-panel-background: $global-background !default;
$offcanvas-box-shadow-left: 7.5px 0 17.5px rgba($global-dark, 0.1);
$offcanvas-box-shadow-right: -7.5px 0 17.5px rgba($global-dark, 0.1);
$offcanvas-close-transition: transform 0.25s easeInOutCubic;

$offcanvas-panel-width: 300px !default;

//
// Component
//

@mixin offCanvas() {
  // Body styles to prevent scrolling
  .has-openOffCanvas {
    height: 100%;
    overflow: hidden;
  }

  // 1. JS styles
  .offCanvas {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: $offcanvas-background;
    z-index: 10;

    // 1.
    opacity: 0;
    display: none;

    &--right {
      .offCanvas-panel {
        box-shadow: $offcanvas-box-shadow-right;
        left: auto;
        right: 0;
        transform: translateX(100%);
      }
    }

    &-panel {
      @include trimChildren();

      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: $offcanvas-panel-background;
      padding: $global-whitespace-large;
      box-shadow: $offcanvas-box-shadow-left;
      z-index: 5;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
      // GSAP
      transform: translateX(-100%);

      @media (min-width: $global-viewport-medium) {
        width: $offcanvas-panel-width;
      }

      @if (mixin-exists(hook-offcanvas-panel)) { @include hook-offcanvas-panel(); }
    }

    &-close {
      position: absolute;
      top: $global-whitespace-large;
      right: $global-whitespace-large;
      color: $global-muted;
      z-index: 10;

      > .icon {
        transition: $offcanvas-close-transition;
        transform-origin: center center;
      }

      &:hover,
      &:focus {
        color: $global-font-color;

        > .icon {
          transform: scale(1.15);
        }
      }
    }

    @if (mixin-exists(hook-offcanvas)) { @include hook-offcanvas(); }
  }
}
