//
// Variables
//

$alert-close-transition: transform 0.25s easeInOutCubic !default;
$alert-border-width: 2px !default;

$alert-primary-enable: true !default;
$alert-primary-background: lighten($global-primary, 43.5) !default;
$alert-primary-color: $global-primary !default;
$alert-primary-border: $global-primary !default;

$alert-success-enable: true !default;
$alert-success-background: lighten($global-success, 58) !default;
$alert-success-color: $global-success !default;
$alert-success-border: $global-success !default;

$alert-danger-enable: true !default;
$alert-danger-background: lighten($global-danger, 37.5) !default;
$alert-danger-color: $global-danger !default;
$alert-danger-border: $global-danger !default;

//
// Component
//

@mixin alert() {
  .alert {
    display: flex;
    padding: $global-whitespace-regular;
    border-radius: $global-border-radius;

    // Reset emphasis and link colors
    a,
    a:hover,
    a:focus {
      color: currentColor;
    }

    a:not([class]),
    .u-link {
      @extend .u-textUnderline;
    }

    > :last-child {
      margin-bottom: 0;
    }

    @if $alert-primary-enable {
      &--primary {
        background: $alert-primary-background;
        border: $alert-border-width solid $alert-primary-border;
        color: $alert-primary-color;

        @if (mixin-exists(hook-alert-primary)) { @include hook-alert-primary(); }
      }
    }

    @if $alert-success-enable {
      &--success {
        background: $alert-success-background;
        border: $alert-border-width solid $alert-success-border;
        color: $alert-success-color;

        @if (mixin-exists(hook-alert-success)) { @include hook-alert-success(); }
      }
    }

    @if $alert-danger-enable {
      &--danger {
        background: $alert-danger-background;
        border: $alert-border-width solid $alert-danger-border;
        color: $alert-danger-color;

        @if (mixin-exists(hook-alert-danger)) { @include hook-alert-danger(); }
      }
    }

    &-content {
      @include trimChildren();

      flex: 0 1 100%;

      > svg {
        margin-right: $global-whitespace-small;
      }
    }

    &-right {
      flex: 0 0 auto;
      padding-left: $global-whitespace-small;
    }

    &-close {
      display: inline-block;
      color: currentColor;

      .icon {
        transition: $alert-close-transition;
        transform-origin: center center;
      }

      &:hover,
      &:focus {
        color: currentColor;

        .icon {
          transform: scale(1.15);
        }
      }
    }

    &--fullWidth {
      border-radius: 0;
      border-left: 0;
      border-right: 0;
    }

    @if (mixin-exists(hook-alert)) { @include hook-alert(); }
  }

  // Add margin if adjacent element
  * + .alert {
    margin-top: $global-whitespace-regular;
  }
}
