//
// Variables
//

$section-vertical-padding: ($global-whitespace-xlarge + $global-whitespace-large) !default;

//
// Component
//

@mixin section() {
  .section {
    @include trimChildren();

    padding: $section-vertical-padding 0;

    @if (mixin-exists(hook-section)) { @include hook-section(); }
  }
}
