//
// Variables
//

$button-font-weight: $global-font-weight-bold !default;
$button-box-shadow: $global-box-shadow !default;
$button-active-box-shadow: $global-box-shadow-inset !default;
$button-border-width: 1px !default;
$button-transition: all 0.25s easeInOutCubic !default;
$button-background-size-active-enable: true !default;
$button-background-size-active: 200% 200% !default;

$button-border-radius: $global-border-radius !default;
$button-small-border-radius: $global-border-radius !default;

$button-small-enable: true !default;
$button-link-enable: true !default;

$button-font-size: $global-font-size !default;
$button-font-size-small: $global-font-size-small !default;

$button-blank-color: $global-font-color !default;
$button-blank-color-hover: $global-font-color !default;

$button-default-enable: true !default;
$button-default-color: $global-font-color !default;
$button-default-background-color: $global-panel-background !default;
$button-default-background: linear-gradient(to bottom right, $button-default-background-color, darken($button-default-background-color, 8.5)) !default;
$button-default-border: darken($button-default-background-color, 15.5) !default;

$button-primary-enable: true !default;
$button-primary-color: $global-contrast !default;
$button-primary-background-color: $global-primary !default;
$button-primary-background: linear-gradient(to bottom right, $button-primary-background-color, darken($button-primary-background-color, 7.5)) !default;
$button-primary-border: darken($button-primary-background-color, 7.5) !default;

$button-secondary-enable: true !default;
$button-secondary-color: $global-contrast !default;
$button-secondary-background-color: $global-secondary !default;
$button-secondary-background: linear-gradient(to bottom right, $button-secondary-background-color, darken($button-secondary-background-color, 7.5)) !default;
$button-secondary-border: darken($button-secondary-background-color, 7.5) !default;

$button-danger-enable: true !default;
$button-danger-color: $global-contrast !default;
$button-danger-background-color: $global-danger !default;
$button-danger-background: linear-gradient(to bottom right, $button-danger-background-color, darken($button-danger-background-color, 12.5)) !default;
$button-danger-border: darken($button-danger-background-color, 12.5) !default;

$button-outline-enable: true !default;
$button-outline-background: $global-background !default;
$button-outline-background-hover: $button-default-background-color !default;
$button-outline-color: $button-default-color !default;
$button-outline-color-hover: $button-default-color !default;
$button-outline-border: $button-default-border !default;

$button-outline-primary-enable: true !default;
$button-outline-primary-background: $global-background !default;
$button-outline-primary-background-hover: $global-primary-light !default;
$button-outline-primary-color: $button-primary-background-color !default;
$button-outline-primary-color-hover: $button-primary-background-color !default;
$button-outline-primary-border: $button-primary-background-color !default;

$button-contrast-enable: true !default;
$button-contrast-color: $global-contrast !default;
$button-contrast-background-color: rgba($global-contrast, 0.15) !default;
$button-contrast-background: linear-gradient(to bottom right, $button-contrast-background-color, rgba($button-contrast-background-color, 0.25)) !default;

$button-contrast-primary-color: $global-primary !default;
$button-contrast-primary-background: $global-background !default;

$button-loader-enable: true !default;
$button-loader-size: 1.5em !default;

$button-icon-enable: true !default;

//
// Component
//

@mixin button() {
  // 1. Correct inability to style clickable `input` types in iOS.
  // 2. Remove margins in Chrome, Safari and Opera.
  // 3. Normalizes styles for `button`.
  // 4. Address `overflow` set to `hidden` in IE 8/9/10/11.
  // 5. Correct `font` properties and `color` not being inherited for `button`.
  // 6. Address inconsistent `text-transform` inheritance which is only inherit in Firefox and IE
  // 7. Style
  // 8. `line-height` is used to create a height
  // 9. `min-height` is necessary for `input` elements in Firefox and Opera because `line-height` is not working.
  // 10. Reset button group whitespace hack
  // 11. Required for `a`.
  // 12. Do not wrap buttons
  // 13. Disable `user-select`
  // 14. Removes inner padding and border in Firefox 4+.
  //
  .button {
    // 1
    appearance: none;
    // 2
    margin: 0;
    // 3
    border: $button-border-width solid transparent;
    // 4
    overflow: visible;
    // 5
    font: inherit;
    font-weight: $button-font-weight;
    color: $button-blank-color;
    // 6
    text-transform: none;
    // 7
    display: inline-block;
    padding: 0 $global-ui-offset;
    background-color: transparent;
    vertical-align: middle;
    cursor: pointer;
    border-radius: $button-border-radius;
    transition: $button-transition;
    background-size: 100% 100%;
    position: relative;
    // 8
    line-height: ($global-height - ($button-border-width * 2));
    // 9
    height: $global-height;
    // 10
    font-size: $button-font-size;
    // 11
    text-decoration: none;
    text-align: center;
    // 12
    white-space: nowrap;
    // 13
    user-select: none;

    &:hover,
    &:focus,
    &:active,
    &.is-active {
      color: $button-blank-color-hover;

      @if $button-background-size-active-enable {
        background-size: $button-background-size-active !important;
      }
    }

    // 14
    &::-moz-focus-inner {
      border: 0;
      padding: 0;
    }

    @if $global-contrast-enable and $button-contrast-enable {
      .u-contrast & {
        &:not(.button--noContrast) {
          color: $button-contrast-color;

          &:hover,
          &:focus,
          &:active,
          &.is-active {
            color: $button-contrast-color;
          }
        }
      }
    }

    @if $button-small-enable {
      &--small {
        padding: 0 $global-ui-offset-small;
        font-size: $button-font-size-small;
        line-height: ($global-height-small - ($button-border-width * 2));
        height: $global-height-small;
        border-radius: $button-small-border-radius;

        @if (mixin-exists(hook-button-small)) { @include hook-button-small(); }
      }
    }

    @if $button-default-enable {
      &--default {
        background-image: $button-default-background;
        border-color: $button-default-border;
        box-shadow: $button-box-shadow;
        color: $button-default-color;

        &:hover,
        &:focus,
        &:active,
        &.is-active {
          color: $button-default-color;
        }

        &:active,
        &.is-active {
          box-shadow: $button-active-box-shadow;
        }

        @if $global-contrast-enable and $button-contrast-enable {
          .u-contrast & {
            &:not(.button--noContrast) {
              background-image: $button-contrast-background;
              color: $button-contrast-color;
              border-color: currentColor;
              box-shadow: none;

              &:hover,
              &:focus,
              &:active,
              &.is-active {
                color: $button-contrast-color;
                box-shadow: none;
              }
            }
          }
        }

        @if (mixin-exists(hook-button-default)) { @include hook-button-default(); }
      }
    }

    @if $button-outline-enable {
      &--outline {
        background: $button-outline-background;
        border-color: $button-outline-border;
        color: $button-outline-color;
        position: relative;
        z-index: 2;
        box-shadow: $button-box-shadow;

        &:hover,
        &:focus,
        &:active,
        &.is-active {
          background: $button-outline-background-hover;
          color: $button-outline-color-hover;
        }

        @if (mixin-exists(hook-button-outline)) { @include hook-button-outline(); }
      }
    }

    @if $button-outline-primary-enable {
      &--outlinePrimary {
        background: $button-outline-primary-background;
        border-color: $button-outline-primary-border;
        color: $button-outline-primary-color;
        position: relative;
        z-index: 2;
        box-shadow: $button-box-shadow;

        &:hover,
        &:focus,
        &:active,
        &.is-active {
          background: $button-outline-primary-background-hover;
          color: $button-outline-primary-color-hover;
        }

        @if (mixin-exists(hook-button-outline-primary)) { @include hook-button-outline-primary(); }
      }
    }

    @if $button-primary-enable {
      &--primary {
        background-image: $button-primary-background;
        border-color: $button-primary-border;
        box-shadow: $button-box-shadow;
        color: $button-primary-color;

        &:hover,
        &:focus,
        &:active,
        &.is-active {
          color: $button-primary-color;
        }

        &:active,
        &.is-active {
          box-shadow: $button-active-box-shadow;
        }

        @if $global-contrast-enable and $button-contrast-enable {
          .u-contrast & {
            &:not(.button--noContrast) {
              background: $button-contrast-primary-background;
              color: $button-contrast-primary-color;
              border: 0;
              box-shadow: none;

              &:hover,
              &:focus,
              &:active,
              &.is-active {
                color: $button-contrast-primary-color;
              }
            }
          }
        }

        @if (mixin-exists(hook-button-primary)) { @include hook-button-primary(); }
      }
    }

    @if $button-secondary-enable {
      &--secondary {
        background-image: $button-secondary-background;
        border-color: $button-secondary-border;
        box-shadow: $button-box-shadow;
        color: $button-secondary-color;

        &:hover,
        &:focus,
        &:active,
        &.is-active {
          color: $button-secondary-color;
        }

        &:active,
        &.is-active {
          box-shadow: $button-active-box-shadow;
        }

        @if (mixin-exists(hook-button-secondary)) { @include hook-button-secondary(); }
      }
    }

    @if $button-danger-enable {
      &--danger {
        background-image: $button-danger-background;
        border-color: $button-danger-border;
        box-shadow: $button-box-shadow;
        color: $global-contrast;

        &:hover,
        &:focus,
        &:active,
        &.is-active {
          color: $global-contrast;
        }

        &:active,
        &.is-active {
          box-shadow: $button-active-box-shadow;
        }

        @if (mixin-exists(hook-button-danger)) { @include hook-button-danger(); }
      }
    }

    &--fullWidth {
      width: 100%;
    }

    @if $button-icon-enable {
      &--icon {
        border-radius: 50%;
        padding: 0;
        width: $global-height;

        svg {
          size: $icon-size-large;
        }

        @if $button-small-enable {
          &.button--small {
            width: $global-height-small;

            svg {
              size: $icon-size-large;
            }
          }
        }
      }
    }

    @if $button-loader-enable {
      > .loader {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        size: $button-loader-size;

        svg {
          size: $button-loader-size;
        }
      }

      &.is-loading {
        span:not(.loader) {
          color: transparent;
        }

        > .loader {
          opacity: 1;
        }
      }
    }

    &.is-disabled,
    &[disabled] {
      opacity: $global-disabled-opacity;
      cursor: not-allowed;
      pointer-events: none;
    }

    @if (mixin-exists(hook-button)) { @include hook-button(); }
  }
}
